import Vue from 'vue';
import Vuex from 'vuex'

Vue.use(Vuex)


const store =new Vuex.Store({
    state:{
        topics:[
            {name:'all',desc:'全部'},
            {name:'good',desc:'精华'},
            {name:'share',desc:'分享'},
            {name:'ask',desc:'问答'},
            {name:'job',desc:'招聘'},
            {name:'dev',desc:'测试'}
        ],
        isShowMoreMenus:false , //actionsheet三个点是否默认显示
        //如果是未登录状态显示登录，如果是已登录状态显示个人中心，消息通知，登出
        isLogined:false, //登录状态，默认未登录
        unLoginedMenus:{
            login:'登录'
        },
        loginedMenus:{
            center:'个人中心',
            msg:'消息通知',
            create:'发表新话题',
            logout:'登出'
        },
        loginname:'' ,//登录用户名
        accesstoken:'', //登录token
        authorId:'' //登录id
    },
    getters:{
        //判断是否二级菜单的登录状态
        moreMenus(state){
            if(state.isLogined){
                return state.loginedMenus
            }
            return state.unLoginedMenus
        }
    },
    mutations:{
        //显示隐藏右侧三个点菜单
        mutationIsShowMoreMenus:function(state,playload){
            state.isShowMoreMenus=playload
        },
        //修改登录状态
        mutationIsLogined(state){
            state.isLogined=true
        },
        // 修改登录名
        mutationLoginName( state,p){
            state.loginname=p
        },
        // 修改token
        mutationaccesstoken( state,p){
            state.accesstoken=p
        },
        // 修改id
        mutationauthorId( state,p){
            state.authorId=p
        },
        // 登出操作{
            mutataionLogout(state){
                state.isLogined=false;
                state.loginname='';
                state.accesstoken=''
                state.authorId=''
            }
        

    },
    actions:{},
})
export default store


